<template>
  <view>
    <!-- 弹出层 -->
    <view v-if="isShow" class="time-picker">
      <view class="time-title">
        <span @click="cancel">取消</span>
        <span>{{ title }}</span>
        <span @click="confirm" :class="{ disabled: !selectedTime }">确定</span>
      </view>
      <scroll-view class="time-scroll" scroll-y>
        <view
          class="time-item"
          v-for="(time, index) in timeSlots"
          :key="index"
          :class="{ active: selectedTime === time }"
          @click="selectTime(time)"
        >
          {{ time }}
        </view>
      </scroll-view>
    </view>
    <view
      v-if="isShow"
      class="scroll-mask"
      @click="isMask ? cancel() : ''"
    ></view>
  </view>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '请选择预约时间段',
    },
    timeSlots: {
      type: Array,
      required: true,
    },
    isMask: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      isShow: false,
      selectedTime: '',
    };
  },
  methods: {
    open() {
      this.isShow = true;
    },
    cancel() {
      this.isShow = false;
    },
    confirm() {
      if (!this.selectedTimeIndex) {
          uni.showToast({
            title: this.title,
            icon: 'none',
          });
          return;
        }
        const time = this.selectedDay.timeList[this.selectedTimeIndex];
        this.close();
        this.selectedTime = time.time; // 将选定的时间段赋值给selectedTime
        this.$emit('change', {
          day: this.selectedDay.day,
          time: time.time,
          startHour: time.start,
          endHour: time.end,
          value: `${this.selectedDay.day} ${time.time}`,
        });
    },
    selectTime(time) {
      this.selectedTime = time;
    },
  },
};
</script>

<style scoped>
.time-picker {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  z-index: 999;
}

.time-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-weight: bold;
  border-bottom: 1px solid #eaeaea;
}

.time-title span {
  font-size: 16px;
  color: #007aff;
}

.time-title .disabled {
  color: #ccc;
  pointer-events: none;
}

.time-scroll {
  max-height: 200px;
  padding: 10px;
}

.time-item {
  padding: 10px;
  font-size: 14px;
  color: #333;
  cursor: pointer;
}

.time-item.active {
  background-color: #007aff;
  color: #fff;
}

.scroll-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 998;
}
</style>